import React from "react";
import { Block, BlockTitle, ListInput, List, Button, Card, ListItem } from 'framework7-react';
import ProfileList from "./list"


const ProfileShow = (props) => {


  if (props.profile) {
    console.log(props.profile.profile.wallet)
    return (
      <React.Fragment>
        <BlockTitle>User:</BlockTitle>
        <Block strong>
          <p>{props.profile.profile.fullname}</p>
        </Block>
        <Card>
        <List inlineLabels noHairlinesMd>
          <ListItem title='Wallet' after={props.profile.profile.wallet}></ListItem>
          <ListInput
            label="Charge"
            type="text"
            defaultValue='0'
            onInput={(e) => {
              props.handleChange({ amount: e.target.value })
            }}
          />
          <Button className="col" fill onClick={props.submit}>Submit</Button>
        </List>
        </Card>
        <BlockTitle>Friends</BlockTitle>
        <ProfileList profiles={props.profile.friends} />

      </React.Fragment>
    )
  } else {
    return (null)
  }
}
export default ProfileShow;
