import React, { Component } from 'react';
import {
  Page,
  Navbar,
  List,
  ListItem,
  ListInput,
  Link,
  Tabs,
  Toolbar,
  Tab,
  Range,
  Block,
  Icon, Fab
} from 'framework7-react';
import { dict } from '../../Dict';
import ModelStore from "../../stores/ModelStore";
import * as MyActions from "../../actions/MyActions";
import ProfileShow from "../../containers/profiles/show"

export default class Layout extends Component {
  constructor() {
    super();
    this.getInstance = this.getInstance.bind(this);
    this.submit = this.submit.bind(this);
    this.handleChangeValue = this.handleChangeValue.bind(this);
    
    this.state = {
      profile: null,
      id: null,
      actuals: null,
      metas: null,
      amount: 0,
      token: window.localStorage.getItem('token'),
    }
  }

  componentWillMount() {
    ModelStore.on("got_instance", this.getInstance);
  }

  componentWillUnmount() {
    ModelStore.removeListener("got_instance", this.getInstance);
  }

  componentDidMount() {
    MyActions.getInstance('profiles', this.$f7route.params['profileId'], this.state.token);
  }

  getInstance() {
    var profile = ModelStore.getIntance()
    var klass = ModelStore.getKlass()
    console.log(klass)
    if (profile && klass === 'Profile') {
      this.setState({
        profile: profile,
        id: profile.profile.id,
      });
    }
  }

  fab() {
    if (this.state.profile) {
      return (
        <Fab href={"/profiles/" + this.state.profile.id + "/edit"} target="#main-view" position="left-bottom" slot="fixed" color="lime">
          <Icon ios="f7:edit" aurora="f7:edit" md="material:edit"></Icon>
          <Icon ios="f7:close" aurora="f7:close" md="material:close"></Icon>
        </Fab>
      )
    }
  }

  handleChangeValue(obj) {
    this.setState(obj);
  }

  submit() {
    var data = {user_id: this.state.id, amount: this.state.amount}
    MyActions.setInstance('wallets/charge', data, this.state.token);
    MyActions.getInstance('profiles', this.$f7route.params['profileId'], this.state.token);
  }

  render() {
    const { profile, amount } = this.state;
    return (
      <Page>
        <Navbar title={dict.profiles} />
        <ProfileShow profile={profile} submit={this.submit} amount={amount} handleChange={this.handleChangeValue}/>
      </Page>
    );
  }
}
