import React from "react";
import { BlockTitle, Block, Button, Row, Col} from 'framework7-react';
import crypto from 'crypto-js';
import { dict } from "../../Dict";
import Moment from 'react-moment';
import 'moment-timezone';
import 'moment/locale/fa';

const PleadingList = (props) => {
  console.log(props)
  if (props.pleadings) {
    return (
      <React.Fragment>
        {props.pleadings.map((pleading) =>
          <React.Fragment>
            <BlockTitle>{pleading.profile.fullname}</BlockTitle>
            <Block strong inset>
              Amount: {pleading.amount}
              <Row>
                <Col><Button className="col" fill onClick={() => props.accept(pleading.id)}>{dict.accept}</Button></Col>
                <Col></Col>
                <Col> <Button className="col" fill color="red" onClick={() => props.reject(pleading.id)}>{dict.reject}</Button></Col>
              </Row>
             
            </Block>
          </React.Fragment>
        )}
      </React.Fragment>
    )
  } else {
    return (<ul></ul>)
  }
}
export default PleadingList;
