import React from "react"
import { Page,Fab, Icon } from 'framework7-react';
import ModelStore from "../../stores/ModelStore";
import PleadingIndex from "../../containers/pleadings/index"
import * as MyActions from "../../actions/MyActions";
import { dict} from '../../Dict';
import Framework7 from 'framework7/framework7.esm.bundle';


export default class Layout extends React.Component {
  constructor() {
    super();
    this.getList = this.getList.bind(this);
    this.search = this.search.bind(this);
    this.reject = this.reject.bind(this);
    this.accept = this.accept.bind(this);

    
    
    
    this.state = {
      pleadings: null,
      token: window.localStorage.getItem('token'),
      query: null,
    }
  }
  componentWillMount() {
    ModelStore.on("got_list", this.getList);
  }

  componentWillUnmount() {
    ModelStore.removeListener("got_list", this.getList);

  }

  componentDidMount(){
    this.loadData();
  }

  loadData(){
    const f7: Framework7 = Framework7.instance;
    f7.toast.show({ text: dict.receiving, closeTimeout: 2000, position: 'top'});
    MyActions.getList('pleadings', this.state.page);
  }

  search(obj){
    this.setState(obj, () => {
      MyActions.getList('pleadings/search', this.state.page, {q: this.state.query});
  });    
  }

  getList() {
    var pleadings = ModelStore.getList()
    var klass = ModelStore.getKlass()
    if (pleadings && klass === 'Pleading'){
      this.setState({
        pleadings: pleadings,
      });
    }
    console.log(pleadings)
  }

  

  reject(id){
    MyActions.getList('pleadings/reject', this.state.page, {id: id});
  }

  accept(id){
    MyActions.getList('pleadings/accept', this.state.page, {id: id});
  }

  render() {
    const {pleadings} = this.state;
    return(<PleadingIndex pleadings={pleadings} reject={this.reject} accept={this.accept} search={this.search}/>)
  }
}
